import { JSX, Match, Switch, mergeProps, splitProps } from "solid-js";
import { Contain, ContainProps } from "../drawer";
import { Popup, PopupProps } from "../drawer/popup";
import { useStyle } from "./useStyle";
import { Header } from "./header";
import { Button } from "../button";
import { css } from "solid-styled-components";
import { DOMElement } from "solid-js/jsx-runtime";
import { genPrefixClasses, useCacheCss } from "../theme";
import { modalCss } from "./style";
import classNames from "classnames";

interface EventHandler<T, E extends Event> {
  (
    e: E & {
      currentTarget: T;
      target: DOMElement;
    }
  ): void;
}

export type ModalProps = ContainProps & {
  title?: JSX.Element;
  closeIcon?: JSX.Element | null | false;
  width?: string;
  maskClosable?: boolean;
  onClose?: EventHandler<HTMLElement, MouseEvent>;
  centered?: boolean;
  footer?: JSX.Element;
} & Omit<
    PopupProps,
    "title" | "panelTransition" | "panelStyle" | "class" | "classList"
  >;

export const Modal = (props: ModalProps) => {
  const [local, other] = splitProps(props, ["children", "maskClosable"]);
  const merged = mergeProps(
    { appendToBody: true, mask: true, maskClosable: true },
    props
  );
  const { panelTransition } = useStyle();
  let ref: HTMLDivElement | undefined = undefined;

  const cacheCss = useCacheCss(modalCss);
  const classes = () => {
    const cla = classNames(
      cacheCss(),
      genPrefixClasses({
        ["modal-wrap"]: true,
        ["modal-is-centered"]: props.centered,
      })
    );
    return cla;
  };

  return (
    <Contain
      {...merged}
      popup={
        <Popup
          destroyOnClose
          onClick={(e) => {
            if (
              ref &&
              !(e.target === ref || ref.contains(e.target)) &&
              merged.maskClosable
            ) {
              props.onClose?.(e);
            }
          }}
          {...merged}
          panelTransition={panelTransition}
        >
          <div class={classes()}>
            <div
              ref={ref}
              classList={genPrefixClasses({
                modal: true,
              })}
            >
              <Header {...props} />
              <div
                class={css({
                  fontSize: "14px",
                  wordBreak: "break-word",
                })}
              >
                {local.children}
              </div>
              <Switch
                fallback={
                  <footer
                    class={css({
                      paddingTop: "16px",
                      textAlign: "right",
                      gap: "12px",
                      "button+button": {
                        marginLeft: "12px",
                      },
                    })}
                  >
                    <Button onClick={other.onClose}>取消</Button>
                    <Button type="primary">确认</Button>
                  </footer>
                }
              >
                <Match when={props.footer}>{props.footer}</Match>
                <Match when={props.footer === null}>
                  {null}
                </Match>
              </Switch>
            </div>
          </div>
        </Popup>
      }
    />
  );
};
