import { CSSAttribute, css, genStyle } from "../../theme";
import { ComponentToken } from "../../theme/types";

const unit = (val: number) => val + "px";

const prepareComponentToken = (token: ComponentToken<{}>) => ({
  footerBg: "transparent",
  headerBg: token.colorBgElevated,
  titleLineHeight: token.lineHeightHeading5,
  titleFontSize: token.fontSizeHeading5,
  contentBg: token.colorBgElevated,
  titleColor: token.colorTextHeading,

  // internal
  contentPadding: token.wireframe
    ? 0
    : `${unit(token.paddingMD)} ${unit(token.paddingContentHorizontalLG)}`,
  headerPadding: token.wireframe
    ? `${unit(token.padding)} ${unit(token.paddingLG)}`
    : 0,
  headerBorderBottom: token.wireframe
    ? `${unit(token.lineWidth)} ${token.lineType} ${token.colorSplit}`
    : "none",
  headerMarginBottom: token.wireframe ? 0 : token.marginXS,
  bodyPadding: token.wireframe ? token.paddingLG : 0,
  footerPadding: token.wireframe
    ? `${unit(token.paddingXS)} ${unit(token.padding)}`
    : 0,
  footerBorderTop: token.wireframe
    ? `${unit(token.lineWidth)} ${token.lineType} ${token.colorSplit}`
    : "none",
  footerBorderRadius: token.wireframe
    ? `0 0 ${unit(token.borderRadiusLG)} ${unit(token.borderRadiusLG)}`
    : 0,
  footerMarginTop: token.wireframe ? 0 : token.marginSM,
  confirmBodyPadding: token.wireframe
    ? `${unit(token.padding * 2)} ${unit(token.padding * 2)} ${unit(
        token.paddingLG
      )}`
    : 0,
  confirmIconMarginInlineEnd: token.wireframe ? token.margin : token.marginSM,
  confirmBtnsMarginTop: token.wireframe ? token.marginLG : token.marginSM,
});

export const prepareToken = (t: ComponentToken<{}>) => {
  const token = { ...t, ...prepareComponentToken(t) };
  const headerPaddingVertical = token.padding;
  const headerFontSize = token.fontSizeHeading5;
  const headerLineHeight = token.lineHeightHeading5;

  const modalToken = {
    modalHeaderHeight: token
      .calc(token.calc(headerLineHeight).mul(headerFontSize).equal())
      .add(token.calc(headerPaddingVertical).mul(2).equal())
      .equal(),
    modalFooterBorderColorSplit: token.colorSplit,
    modalFooterBorderStyle: token.lineType,
    modalFooterBorderWidth: token.lineWidth,
    modalCloseIconColor: token.colorIcon,
    modalCloseIconHoverColor: token.colorIconHover,
    modalCloseBtnSize: token.controlHeight,
    modalConfirmIconSize: token.fontHeight,
    modalTitleHeight: token
      .calc(token.titleFontSize)
      .mul(token.titleLineHeight)
      .equal(),
  };
  return modalToken;
};

const closeIconCss = (
  token: ComponentToken<ReturnType<typeof prepareToken>>
): CSSAttribute => {
  return {
    [`.${token.componentCls}-close-icon`]: {
      position: "absolute",
      right: 0,
      top:
        token
          .calc(token.modalHeaderHeight)
          .sub(token.modalCloseBtnSize)
          .div(2)
          .equal() + "px",
      insetInlineEnd:
        token
          .calc(token.modalHeaderHeight)
          .sub(token.modalCloseBtnSize)
          .div(2)
          .equal() + "px",
      zIndex: token.calc(token.zIndexPopupBase).add(10).equal() + "px",
      padding: 0,
      color: token.modalCloseIconColor,
      fontWeight: token.fontWeightStrong,
      lineHeight: 1,
      textDecoration: "none",
      background: "transparent",
      borderRadius: token.borderRadiusSM + "px",
      width: token.modalCloseBtnSize + "px",
      height: token.modalCloseBtnSize + "px",
      border: 0,
      outline: 0,
      cursor: "pointer",
      transition: `color ${token.motionDurationMid}, background-color ${token.motionDurationMid}`,
      "&:hover": {
        color: token.modalCloseIconHoverColor,
        backgroundColor: token.colorBgTextHover,
        textDecoration: "none",
      },

      "&:active": {
        backgroundColor: token.colorBgTextActive,
      },
    },
  };
};

export const modalCss = () => {
  return genStyle(
    "modal",
    (token) => {
      console.log("tt", token);
      return css({
        position: "absolute",
        inset: 0,
        overflow: "auto",
        ...closeIconCss(token),
        [`&.${token.componentCls}-is-centered`]: {
          textAlign: "center",
          "&:before": {
            display: "inline-block",
            content: `''`,
            height: "100%",
            verticalAlign: "middle",
          },
          [`.${token.componentCls}`]: {
            top: 0,
            margin: 0,
            display: "inline-block",
            verticalAlign: "middle",
          },
        },
        [`.${token.componentCls}`]: {
          zIndex: token.zIndexPopupBase,
          position: "relative",
          background: "#fff",
          "pointer-events": "auto",
          "border-radius": "2px",
          top: "100px",
          padding: "16px",
          margin: "0 auto",
          width: "300px",
          textAlign: "start",
          "margin-bottom": "24px",
          "box-shadow":
            "0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 9px 28px 8px rgba(0, 0, 0, 0.05)",
        },
      });
    },
    prepareToken
  );
};
